@font-face {
  font-family: "oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswald/oswaldregular.woff2");
  font-display: swap;
}
@font-face {
  font-family: "oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswald/oswaldmedium.woff2");
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lato/Lato-Regular.woff2");
  font-display: swap;
}
:root {
  --color-basic_green: #68b738;
  --color-basic_green-2: #5eaa2f;
  --color-basic_grey: #f2f2f2;
  --color-special_grey: #ebebeb;
  --color-special_grey-2: #cdcdcd;
  --color-special_grey-3: #666666;
  --color-special_grey-4: #eaeaea;
  --color-special_grey-5: #e7e7e7;
  --color-special_grey-6: #e6e6e6;
  --color-special_grey-7: #d9d9d9;
  --color-special_grey-8: #bababa;
  --color-special_dark-grey: #444444;
  --color-special_dark-grey-2: #222222;
  --color-basic-black-2: #111111;
  --color-basic-black: #000000;
  --color-basic-white: #ffffff;
  --color-status_error: #ff8282;
}
input::placeholder {
  font-family: "oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: var(--color-special_dark-grey);
}
textarea::placeholder {
  font-family: "oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: var(--color-special_dark-grey);
}
.main-header {
  width: 320px;
  height: 66px;
}
.main-header-container {
  display: flex;
  flex-wrap: wrap;
  max-width: 280px;
  justify-content: space-between;
  margin: 0 auto;
  padding-top: 10px;
}
.main-header__logo--image:active {
  opacity: 0.6;
}
.main-header__logo--text {
  margin-left: -30px;
  margin-top: 10px;
}
@media (min-width: 768px) {
  .main-header {
    width: 768px;
    height: 77px;
  }
  .main-header-container {
    display: flex;
    flex-wrap: wrap;
    max-width: 708px;
    justify-content: space-between;
    padding: 0;
    padding-top: 25px;
  }
  .main-header__logo--image:active {
    opacity: 0.6;
  }
  .main-header__logo--text {
    display: none;
  }
}
@media (min-width: 1220px) {
  .main-header {
    width: 1440px;
    height: 139px;
  }
  .main-header-container {
    display: flex;
    flex-wrap: wrap;
    max-width: 1220px;
    justify-content: space-between;
    padding-top: 50px;
  }
  .main-header__logo--image:hover {
    opacity: 0.8;
  }
  .main-header__logo--image:active {
    opacity: 0.6;
  }
  .main-header__logo--text {
    display: none;
  }
}
.main-header-border {
  border-bottom: 1px solid var(--color-special_grey-7);
}
@media (min-width: 768px) {
  .main-header-border {
    border-bottom: none;
  }
}
.main-navigation {
  font-family: "oswald", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: var(--color-basic-black);
  position: relative;
}
.main-navigation--closed .main-navigation__toggle {
  background-image: url("../img/header/mobile/logo_button_closed.svg");
  background-repeat: no-repeat;
  position: absolute;
  right: 0;
  top: 10px;
  width: 24px;
  height: 18px;
  background-color: var(--color-basic-white);
  border: none;
  cursor: pointer;
}
.main-navigation--opened .main-navigation__toggle {
  background-image: url("../img/header/mobile/logo_button_opened.svg");
  background-repeat: no-repeat;
  position: absolute;
  right: 0;
  top: 10px;
  width: 25px;
  height: 25px;
  background-color: var(--color-basic-white);
  border: none;
  cursor: pointer;
}
.main-navigation__list {
  min-width: 320px;
  position: absolute;
  left: 0;
}
.main-navigation__list-item {
  padding-top: 23px;
  padding-bottom: 23px;
  border-bottom: 1px solid var(--color-special_grey-6);
}
.main-navigation__list-item:first-child {
  border-top: 1px solid var(--color-special_grey-6);
}
.main-navigation__link {
  color: var(--color-basic-black);
  text-decoration: none;
  padding-top: 23px;
  padding-bottom: 23px;
  cursor: pointer;
  text-transform: uppercase;
}
.main-navigation__link:active {
  opacity: 0.3;
}
@media (max-width: 767px) {
  .main-navigation--closed .main-navigation__list-item {
    display: none;
  }
  .main-navigation__container {
    position: absolute;
    left: -300px;
    top: 54px;
    z-index: 1;
  }
  .main-navigation__list {
    text-align: center;
    background-color: var(--color-basic-white);
  }
  .main-navigation--nojs .main-navigation__container {
    position: absolute;
    left: -300px;
    top: 54px;
    z-index: 1;
  }
}
@media (min-width: 768px) {
  .main-navigation {
    font-family: "oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: var(--color-basic-black);
    vertical-align: middle;
  }
  .main-navigation__toggle {
    display: none;
  }
  .main-navigation__container {
    display: flex;
    flex-wrap: wrap;
    min-width: 441px;
  }
  .main-navigation__list {
    display: flex;
    flex-wrap: wrap;
    min-width: 440px;
    justify-content: space-between;
  }
  .main-navigation__list-item {
    margin-top: 15px;
    padding: 0 0 10px 0;
    border: none;
  }
  .main-navigation__list-item:first-child {
    border-top: none;
  }
  .main-navigation__list-item--current {
    border-bottom: 2px solid var(--color-basic_green);
  }
  .main-navigation__list-item--current-green-white {
    border-bottom: 2px solid var(--color-basic_green);
  }
  .main-navigation__link {
    color: var(--color-basic-black);
    text-decoration: none;
  }
}
@media (min-width: 1220px) {
  .main-navigation {
    font-family: "oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    color: var(--color-basic-black);
  }
  .main-navigation__toggle {
    display: none;
  }
  .main-navigation__container {
    min-width: 478px;
  }
  .main-navigation__list {
    display: flex;
    flex-wrap: wrap;
    min-width: 478px;
    justify-content: space-between;
  }
  .main-navigation__list-item--current {
    border-bottom: 2px solid var(--color-basic_green);
  }
  .main-navigation__list-item--current-green-white {
    border-bottom: 2px solid var(--color-basic-white);
  }
  .main-navigation__link:hover {
    opacity: 0.6;
  }
  .main-navigation__link:active {
    opacity: 0.3;
  }
  .main-navigation__link--white-font {
    color: var(--color-basic-white);
  }
  .main-navigation__list-item:first-child {
    border-top: none;
  }
}
.primary-container {
