  .benefits__item--periodicity::before {
    content: url("../img/benefits/tablet/periodicity-tablet.svg");
  }
  .benefits__item--duration::before {
    content: url("../img/benefits/tablet/duration-tablet.svg");
  }
  .benefits__item--serial-number::after {
    position: absolute;
    top: -40px;
    left: 100px;
    counter-increment: section;
    content: "" counter(section);
    font-family: "oswald", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 280px;
    line-height: 280px;
    color: var(--color-basic_grey);
