  .additional__item:not(:last-child) {
    margin-bottom: 0;
  }
  .additional__mark {
    width: 38px;
    height: 38px;
    margin-right: 19px;
  }
  .additional__input[type="checkbox"]:checked + .additional__mark::before {
    position: absolute;
    top: 10px;
    left: 8px;
    content: url("../img/additional//tablet/checkbox_active_tablet.svg");
  }
  .additional__text {
    font-size: 20px;
    line-height: 30px;
    margin-top: 6px;
  }
}
@media (min-width: 1220px) {
  .additional {
    width: 1440px;
    height: 128px;
    padding: 0;
    margin: 0 0 66px 0;
    border: none;
  }
  .additional__container {
    max-width: 1220px;
    margin: 0;
  }
  .additional__title {
    font-size: 36px;
    line-height: 36px;
    margin: 0 0 62px 0;
  }
  .additional__title::before {
    width: 70px;
    left: -110px;
  }
  .additional__title::after {
    width: 1078px;
  }
  .additional__list {
    display: flex;
    flex-wrap: wrap;
    gap: 0;
  }
  .additional__item:nth-child(1) {
    margin-right: 105px;
  }
  .additional__item:nth-child(2) {
    margin-right: 137px;
  }
  .additional__item:nth-child(3) {
    margin-right: 198px;
  }
  .additional__item:disabled {
    opacity: 0.3;
  }
  .additional__mark:hover {
    border: 1px solid var(--color-special_grey-8);
  }
}
