body {
  font-family: "Lato", sans-serif;
  font-style: normal;
  font-weight: 400;
  min-height: 100%;
  box-sizing: border-box;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.reset-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
